.mainWrap {
    min-height: 50vh;
    text-align: center;
    display: flex;
    border-top: 1px solid var(--black);
    border-bottom: 1px solid var(--black);
    cursor: none;
}
.mainLeft, .mainRight {
    width: 50%;
    height: auto;
    position: relative;
}
.mainLeft > img, .mainRight > img {
    display: block;
    width: 100%;
    height: auto;
    pointer-events: none;
}
.mainLeft {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.btnContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateX(-50%);
}
.mainRight > img {
    position: absolute;
    top: 0;
    left: 0;
}
.mainRight:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 100%;
    z-index: 999;
    background: var(--black);
}
.mainRight.noLine:before {
    display: none;
}
.welcome {
    display: block;
    width: 80%;
    height: 20%;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 999;
}
.mainTicketBtn {
    width: 280px;
    height: 98px;
    margin-top: 30px;
    background: url("/src/assets/mainT.png") no-repeat center center / 100% 100%;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
    .mainWrap {
        cursor: default;
    }
    .mainWrap {
        height: auto;
        flex-wrap: wrap;
    }
    .mainLeft, .mainRight {
        width: 100%;
        height: 100%;
    }
    .mainRight:after {
        width: 100%;
        height: 1px;
    }
    .mainRight > img {
        position: relative;
        z-index: -10;
    }
    .welcome {
        position: absolute;
        top: 200px;
        left: 50%;
        transform: translateX(-50%);
        margin: 140px auto 0;
    }
}
@media screen and (max-width: 767px) {
    .mainWrap {
        height: 100%;
        background: #DFE1FC;
    }
    .mainLeft {
        width: 100vw;
        height: 100%;
        padding-bottom: 0;
        position: relative;
        flex-direction: row;
        align-items: flex-start;
        justify-content: flex-start;
    }
    .mainLeft img {
        display: block;
        width: 100vw;
        height: auto;
    }
    .btnContainer {
        width: 100%;
        top: 40px;
        left: 30px;
        transform: translateX(0);
        flex-direction: row;
        align-items: flex-start;
    }
    .arrow span {
        display: inline-block;
        transform: rotate(-90deg);
    }
    .mainTicketBtnM {
        display: block;
        width: 52%;
        height: 12vh;
        margin-left: auto;
        margin-right: 45px;
        background: none;
    }
}