
:root {
  --main-color: #6933ff;
  --blue: #15AABF;
  --red: #e03131;
  --orange: #ff774a;
  --yellow: #ffcd35;
  --black: #040404;
  --gray--5: #777;
  --gray--4: #adadad;
  --gray--3: #c8c8c8;
  --gray--2: #e1e1e1;
  --gray--1: #f1f3f5;
  --gray--0: #fbfbfb;
  --white: #fff;
  --warm-white: #f1f3f5;
  --font-size--h1: 34px;
  --font-size--h2: 26px;
  --font-size--h3: 1rem;
  --font-size--h4: 14px;
  --font-size--h5: 12px;
  --font-weight--bold: 700;
  --font-weight--regualr: 400;
  --border--main: 1px solid #6933ff;
  --border--blue: 1px solid #15aabf;
  --border--gray: 1px solid #e1e1e1;
  --font-fam--en: 'Montserrat', sans-serif;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Noto Sans KR', sans-serif;
}
html {
  overflow-x: hidden;
}
body {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  letter-spacing: -1px;
  color: var(--black);
}
input:disabled:-webkit-autofill { -webkit-box-shadow: 0 0 0 30px #f1f3f5 inset ; -webkit-text-fill-color: #000; }
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active { transition: background-color 5000s ease-in-out 0s; }
button, textarea, a, input {
  color: var(--black);
}
ul, li {
  list-style: none;
}
a {
  text-decoration: none;
  color: var(--black);
}
em {
  font-style: normal;
}
button, textarea {
  outline: none;
  border: 0;
  background: none;
}
img {
  display: block;
}
button {
  font-size: var(--font-size--h3);
}
input, select {
  height: 45px;
  outline: none;
  border: 1px solid #e1e1e1;
}
input , textarea {
  padding: 12px 15px;
  border: 1px solid var(--gray--2);
}
input::placeholder, textarea::placeholder {
  color: var(--gray--4);
  opacity: 1;
  transition: all 0.35s;
}
input:focus::placeholder, textarea:focus::placeholder {
  opacity: 0;
}
select {
  padding: 0 20px;
}
datalist {
  background-color: white;
  border: 1px solid blue;
}
button, input[type="button"] {
  cursor: pointer;
}
.btn {
  width: 420px;
  height: 55px;
  background: var(--main-color);
  color: var(--white);
}
.btn.success {
  border: 1px solid var(--main-color);
  background: var(--white);
  color: var(--main-color);
}
button:disabled, input[type="button"]:disabled {
  cursor: default;
  background: var(--gray--5);
  color: var(--white);
}
h2 {
  font-size: var(--font-size--h2);
}
*.unselectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none ;
}
.red {
  color: var(--red);
}
.blue {
  color: var(--blue);
}
.ellipsis1 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.ellipsis2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.ellipsis5 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}
.brElement {
  word-break: break-all;
  white-space: pre-wrap;
}
.spanInvalid {
  color: var(--red);
  font-size: var(--font-size--h5);
  margin-top: 10px;
}
.spanCorrect {
  color: var(--blue);
  font-size: var(--font-size--h5);
  margin-top: 10px;
}
.caption {
  font-size: var(--font-size--h5);
  color: var(--gray--5);
  margin-top: 10px;
}
.caption.blue {
  color: var(--blue);
}
.fontFamEn {
  font-family: var(--font-fam--en);
}
.gnb li > a.active {
  color: var(--main-color) !important;
}
.placeholderImg.loading {
  filter: blur(10px);
  clip-path: inset(0);
}
.placeholderImg.loaded {
  filter: blur(0);
  transition: filter .35s linear;
}
.qpickerPagination {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 60px 0;
}
.checkBtn {
  width: 20px;
  height: 20px;
  border: var(--border--gray);
  margin-right: 10px;
}
.checkBtn.active {
  border: 0 !important;
/*  background: var(--main-color) url("/src/assets/images/check-icon.svg") no-repeat center center / 13px 10px ;*/
}
.topBtn {
  position: fixed;
  right: 40px;
  width: 70px;
  height: 70px;
  transform: scale(0);
  /*background: url("/src/assets/images/topBtn.svg") no-repeat center / 100%;*/
}
.topBtn.fixed {
  bottom: 5%;
  opacity: 1;
  transform: scale(1);
  z-index: 999;
}
.topBtn span {
  display: inline-block;
  width: 28px;
  height: 16px;
  /*background: url("/src/assets/images/arrow-down-white.svg") no-repeat center / 100%;*/
  margin-bottom: 17px;
}
.topBtn:hover span {
  animation: arrowTop .5s ease-in-out;
}
.underLine {
  position: relative;
}
.underLine:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: var(--black);
  position: absolute;
  bottom: -2px;
  left: 0;
}
.dimmedEl {
  position: relative;
  z-index: 0;
}
.dimmedEl:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .45);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
@keyframes arrowTop {
  0% { transform: translateY(0); }
  25% {  transform: translateY(-2px); }
  50% { transform: translateY(0); }
  75% { transform: translateY(-2px); }
  100% { transform: translateY(0); }
}
.dimmed {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, .45);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
}
