@keyframes shak {
  0% {
    transform: rotateY(90deg);
  }

  50% {
    transform: rotateY(-90deg);
  }

  100% {
    transform: rotateY(90deg);
  }
}
@keyframes shaking {
  0% {
    transform: rotate(20deg);
  }
  25% {
    transform: rotate(-20deg);
  }
  50% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(20deg);
  }
}
.arrow span {
  display: block;
  width: 14px;
  height: 10px;
  margin: 6px 0;
  /*background: url("/src/assets/images/mainA.svg") no-repeat center / 100%;*/
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}
.arrow.m span {
  animation: arrow 3s infinite;
  -webkit-animation: arrow 3s infinite;
}
@keyframes arrow {
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:1}
  100% {opacity:0}
}
@-webkit-keyframes arrow { /*Safari and Chrome*/
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:1}
  100% {opacity:0}
}
.a1 { animation-delay: -.8s !important; -webkit-animation-delay: -.8s; }
.a2 { animation-delay: -.6s !important; -webkit-animation-delay: -.6s; }
