.arrow.m .a1 { animation-delay: -1.8s !important; -webkit-animation-delay: -1.8s; }
.arrow.m .a2 { animation-delay: -1.6s !important; -webkit-animation-delay: -1.6s; }
.arrow.m .a3 { animation-delay: -1.4s !important; -webkit-animation-delay: -1.4s; }
.arrow.m .a4 { animation-delay: -1.2s !important; -webkit-animation-delay: 1.2s; }
.arrow.m .a5 { animation-delay: -1s !important; -webkit-animation-delay: -1s; }
.arrow.m .a6 { animation-delay: -.8s !important; -webkit-animation-delay: -.8s; }
.arrow.m .a7 { animation-delay: -.6s !important; -webkit-animation-delay: -.6s; }
.arrow.m .a8 { animation-delay: -.4s !important; -webkit-animation-delay: -.4s; }
.arrow.m .a9 { animation-delay: -.2s !important; -webkit-animation-delay: -.2s; }
.arrow.m .a0 { animation-delay: 0s !important; -webkit-animation-delay: 0s; }

